import otherLogin from '../../components/home/other-login.vue';
export default {
	components:{
		//uniStatusBar,
		otherLogin
	},
	data() {
		return {
			disabled:true,
			loading:false,
			isverificationcodelogin:false,
			username:'',
			password:'',
			phone:'',
			verificationcode:'',
			codetime:0,
		}
	},
	watch:{
		username(val){
			this.onloginbuttonchange();
		},
		password(val){
			this.onloginbuttonchange();
		},
		phone(val){
			this.onloginbuttonchange();
		},
		verificationcode(val){
			this.onloginbuttonchange();
		},
		isverificationcodelogin(val){
			this.onloginbuttonchange();
		}
	},
	methods: {
		back(){
			uni.navigateBack({
				delta: 1
			});
		},
		submit(){
			
		},
		verificationCodeLogin(){
			this.isverificationcodelogin=!this.isverificationcodelogin;
		},
		onloginbuttonchange(){
			if((this.isverificationcodelogin && this.phone && this.verificationcode) || 
			(!this.isverificationcodelogin && this.username && this.password)){
				console.log('22222222222222222222');
				this.disabled=false;
				return;
			}
			this.disabled=true;
		},
		getverificationcode(){
			if(this.codetime>0){
				return;
			}
			this.codetime=10;
			let time = setInterval(()=>{
				this.codetime--;
				if(this.codetime<1){
					clearInterval(time);
				}
			},1000);
			
		}
	}
}
